	// 
	Vue.prototype.$timend = function(dateBegin, dateEnd) {
		let dateDiff = new Date(dateEnd).getTime() - new Date(dateBegin).getTime(); //
		//
		let dayDiff = Math.floor(dateDiff / (24 * 3600 * 1000)); //
		let leave1 = dateDiff % (24 * 3600 * 1000) //
		let hours = Math.floor(leave1 / (3600 * 1000)) //
		//
		let leave2 = leave1 % (3600 * 1000) //
		let minutes = Math.floor(leave2 / (60 * 1000)) //
		//
		let leave3 = leave2 % (60 * 1000) //
		let seconds = Math.round(leave3 / 1000)
		return dayDiff + "" + hours + "" + minutes + "" + seconds + "";
	}

	// 
	Vue.prototype.$getDaysInMonth = function(year, month) {
		month = parseInt(month, 10); //parseInt(number,type)210
		var temp = new Date(year, month, 0);
		return temp.getDate();
	}

	// 
	Vue.prototype.$changemont = function(datetime, more) {
		let year = datetime.split('-')[0] * 1;
		let mon = datetime.split('-')[1] * 1;
		if (more == 'jian') {
			if (mon > 1) {
				--mon;
			} else {
				--year;
				mon = 12;
			}
		} else {
			if (mon < 12) {
				mon++;
			} else {
				year++;
				mon = 1;
			}
		}
		mon = mon < 10 ? '0' + mon : mon;
		return year + '-' + mon;
	}

	Vue.prototype.$hidePhone = function(cellValue) {
		if (Number(cellValue) && String(cellValue).length === 11) {
			var mobile = String(cellValue)
			var reg = /^(\d{3})\d{4}(\d{4})$/
			return mobile.replace(reg, '$1****$2')
		} else {
			return cellValue
		}
	}

	// 
	Vue.prototype.$changedate = function(datetime, daymonth, more) {
		let year = datetime.split('-')[0] * 1;
		let mon = datetime.split('-')[1] * 1;
		let dauy = datetime.split('-')[2] * 1;
		if (more == 'add') {
			if (dauy < daymonth) {
				dauy++;
			} else {
				dauy = 1;
				if (mon < 12) {
					mon++;
				} else {
					mon = 1;
					year++;
				}
			}
			if (mon < 10) {
				mon = '0' + mon
			}
			if (dauy < 10) {
				dauy = '0' + dauy
			}
		} else if (more == 'jian') {
			if (dauy > 1) {
				dauy--;
			} else {
				dauy = daymonth;
				if (mon > 1) {
					mon--;
				} else {
					mon = 12;
					year--;
				}
			}
			if (mon < 10) {
				mon = '0' + mon
			}
			if (dauy < 10) {
				dauy = '0' + dauy
			}
		}
		return year + "-" + mon + "-" + dauy;
	}

